<template>
  <div id="app">
    <Table :cards="cards" />
  </div>
</template>

<script>
import Table from "../../components/Table";

export default {
  name: "App",
  components: {
    Table
  },
  methods: {
    initCards() {
      let self = this;
      this.$http.get("/data/data.json").then(result => {
        let cards = result.data;
        let list = [];
        for (let i in cards) {
          cards[i]["transform"] =
            "rotate(" + (Math.random() * 360).toFixed(2) + "deg)";
          cards[i]["ori_transform"] =
            "rotate(" + (Math.random() * 360).toFixed(2) + "deg)";
          cards[i]["left"] =
            Math.abs(
              document.body.clientWidth * Math.random().toFixed(2) - 273
            ).toFixed(2) + "px";
          cards[i]["top"] =
            Math.abs(
              document.body.clientHeight * Math.random().toFixed(2) - 273
            ).toFixed(2) + "px";
          cards[i]["color"] =
            Math.floor(Math.random() * 255) +
            "," +
            Math.floor(Math.random() * 255) +
            "," +
            Math.floor(Math.random() * 255);
          list.push(cards[i]);
        }
        self.cards = list;
      });
    }
  },
  mounted() {
    this.initCards();
  },
  data() {
    return {
      cards: []
    };
  }
};
</script>

