<template>
  <div
    class="card"
    @mouseover="card.transform = 'rotate(0deg) scale(1.05)'"
    @mouseleave="card.transform=card.ori_transform"
    @click="onClick(card)"
    :style="{ transform:card.transform, left:card.left, top:card.top }"
  >
    <div class="image" :style="{ background:'rgb(' + card.color + ')' }"></div>
    <div class="content">
      <div class="title">{{card.title}}</div>
      <div class="date">{{card.date}}</div>
    </div>
  </div>
</template>

<script>
import drag from "@branu-jp/v-drag";

export default {
  name: "Card",
  props: {
    card: {
      type: Object,
      required: true
    }
  },
  directives: {
    drag
  },
  methods: {
    encode(param) {
      try {
        return encodeURIComponent(btoa(param));
      } catch (error) {
        // console.log(error);
      }
      return "";
    },
    onClick(card) {
      window.location.href = '/markdown?id=' +this.encode(card.id) + '&bg='+this.encode(card.color)
    }
  }
};
</script>

<style scoped>
.card {
  z-index: 1;
  width: 160px;
  height: 200px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 6px;
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  transition: transform 0.2s;
}
.card:hover {
  z-index: 2;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2), 0 10px 26px 0 rgba(0, 0, 0, 0.19);
  transition: 1s;
  transform: rotate(0deg) scale(1.1);
}
.card .image {
  border-radius: 6px;
  height: 100%;
}
.card .image > img {
  border-radius: 6px;
  min-width: 100%;
  max-width: 100%;
  max-height: 100%;
}
.card .content {
  border-radius: 0 0 6px 6px;
  position: inherit;
  top: 110px;
  width: 100%;
  height: 90px;
  background: rgba(255, 255, 255, 0.85);
}
.card .content .title {
  padding: 10px;
  font-size: 14px;
  text-align: left;
}

.card .content .date {
  position: inherit;
  right: 10px;
  bottom: 5px;
  font-size: 13px;
  font-style: italic;
}
</style>
